@font-face {
  font-family: "Averta Demo PE Cutted Demo";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.astudio-newsletter {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f4f9ff;
  border-radius: 75px;
  margin: 0 123px 0 120px;
}
.astudio-newsletter-b1 h2 {
  font-family: "Averta Demo PE Cutted Demo";
  font-size: 40px;
  line-height: 49.44px;
  color: #000000;
  margin: 98px 0 17px 0;
}
.astudio-newsletter-b1 p {
  font-family: "Averta Demo PE Cutted Demo";
  font-weight: 400;
  font-family: 18px;
  line-height: 28px;
  color: #757575;
  margin-bottom: 99px;
}
.astudio-newsletter-b2 {
  position: relative;
  left: 12.5rem;
  background-image: url(".../../../../../images/Rectangle 31.png");
  background-repeat: no-repeat;
  height: 292px;
  width: 543px;
}
.astudio-newsletter-b2-dotImg {
  position: absolute;
  z-index: -5;
  top: -5rem;
  left: 22rem;
  width: 175px;
  height: 154px;
  background-image: url(".../../../../../images/Dot 1.png");
}
.astudio-newsletter-b2-input input {
  height: 80px;
  border: 1px solid #f1f1f1;
  border-radius: 60px;
  margin-left: -95px;
  margin-top: 106px;
  box-shadow: 10px 20px 50px rgba(0, 0, 0, 0.15);
}
.astudio-newsletter-b2-input ::-webkit-input-placeholder {
  font-family: "Averta Demo PE Cutted Demo";
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #959595;
}
.astudio-newsletter-b2-button {
  width: 121px;
  background: #2639ed;
  border-radius: 60px;
  padding: 20px 40px;
  text-align: center;
  position: relative;
  top: -4.4rem;
  left: 11rem;
}
.astudio-newsletter-b2-button a {
  text-decoration: none;
  color: #ffffff;
  font-family: "Averta Demo PE Cutted Demo";
}
.astudio-newsletter-b2-button a:hover {
  text-decoration: none;
}

.bgcutImg {
  height: 178px;
  width: 178px;
  position: relative;
  top: 21rem;
  left: 4rem;
  z-index: -5;
  background: url(".../../../../../images/Rectangle 32.png");
}